@import (less) 'color.less'; 

.button-icon { font-size: 2em !important;
  // 16px*2=32px; 
  color:@black; 
  display: inline-block; 
  vertical-align: middle; 
}

.nbp-ring-chart{
  //height:100%;
  width:100%;
  padding: 10px;
}

.svg-preserve-aspect-container {
  // display: inline-block;
  width: 100%;
  height:100%;
  //min-height: 200px; /* default icon height */
  position:relative;
  // user-select: none;
}

.svg-preserve-aspect-canvas {
  display: block;
  width: 100%;
  height: 100%;
  visibility: hidden;
}

.svg-preserve-aspect {
  width: 100%;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
}

.legend-label{
  font-family: sans-serif; 
  font-size: 1em; 
}
.summary-text {
  font-family: sans-serif; 
  font-size: 1em;

}
.summary-value {
  font-weight: bold; 
}

.center-title {
  font-size: 1.2em; 
  font-weight: bold;
}
.pointer-cursor{
  cursor: pointer;
}
.center-subtitle {
  .pointer-cursor;
  font-size: 0.8em;
  fill: lightgreen; 
  font-weight: bold;
}

h3 {
  margin-top: 1px;
}
.chart-heading {
  margin-top: 5px;
  margin-bottom: 15px; 

}